.flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.flexColumn {
  display: flex;
  flex-direction: column;
}
// .button {
//   border: none;
// }
.order-confirm-wrapper {
  padding: 40px 45px;
  color: #000;
  .head-title {
    font-size: 14px;
    font-weight: bold;
  }
  .class-info {
    .course-amount {
      margin-right: 5px;
      font-weight: 600;
      color: #000;
      font-size: 20px;
      font-family: DINAlternate-Bold, DINAlternate;
    }
    .class-detail {
      margin-top: 20px;
      background: #fafafa;
      border-radius: 4px;
      font-size: 14px;
      padding: 30px 24px;
      .flex();
      .amount {
        font-size: 30px;
        color: #e74938;
        font-weight: bold;
      }
    }
  }
  .discount-rights {
    margin-top: 40px;
    .rights-list {
      width: 100%;
      margin-top: 20px;
      list-style-type: none;
      .flex();
      align-items: center;
      flex-wrap: wrap;
      li {
        width: 49%;
        height: 80px;
        padding: 0 24px;
        background: #fafafa;
        border-radius: 4px;
        border: 1px solid transparent;
        &.active {
          border: 1px solid #025eff;
          // background: #fff;
        }
        &:nth-child(odd) {
          margin-right: 20px;
        }
        .flex();
        align-items: center;
        .list-left {
          .flex();
          align-items: center;
          .discout {
            font-size: 30px;
            font-weight: bold;
            font-family: DINAlternate-Bold, DINAlternate;
          }
          .line {
            margin: 0 24px;
            display: block;
            width: 1px;
            height: 50px;
            border-left: 1px dashed #bfbfbf;
          }
          .list-name {
            p {
              font-size: 16px;
              font-weight: bold;
              &.count {
                margin-top: 8px;
                font-size: 12px;
                color: #666;
                font-weight: 400;
              }
            }
          }
        }
        .list-right {
          width: 20px;
          .inherit {
            color: #dfdfdf;
            font-size: 20px;
            &.active {
              color: #025eff;
            }
          }
        }
      }
    }
    .warn-text {
      display: inline-block;
      margin-top: 14px;
      padding: 3px 11px;
      background: #fff2e7;
      border-radius: 4px;
      font-size: 12px;
      color: #e97000;
    }
  }
  .pay-amount {
    margin-top: 16px;
    border-top: 1px dashed #dedede;
    padding: 56px 0 63px 0;
    .flex();
    align-items: center;
    .pay-left {
      font-size: 14px;
      span {
        margin-right: 5px;
        font-size: 30px;
        color: #e74938;
        font-weight: bold;
        font-family: DINAlternate-Bold, DINAlternate;
      }
      i {
        margin-left: 6px;
        font-size: 14px;
        color: #999;
        text-decoration: line-through;
        font-family: PingFang SC;
        font-style: normal;
      }
    }
    .btn {
      width: 216px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 20px;
      color: #fff;
      border: none;
      background: #e74938;
      border-radius: 25px;
    }
  }
}
