
.flex{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 45px;
}
.pay-order {
  padding: 40px 45px;
  color: #000;
  .title {
    font-size: 14px;
    font-weight: bold;
  }
  .class-info {
    .flex();
    align-items: center;
    margin-top: 20px;
    padding: 20px 24px;
    background: #FAFAFA;
    border-radius: 4px;
    font-size: 14px;
    .order-NO {
      margin-top: 10px;
    }
    .info-right {
      font-weight: bold;
      span {
        margin-right: 3px;
        font-size: 30px;
        color: #E74938;
        font-family: DINAlternate-Bold, DINAlternate;
      }
    }
  }
}
